@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 6rem;
}

.app-light{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.5);
  color: rgb(0, 0, 0); 
  content: '';
  background: url('./assets/p3.webp') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  margin-top: 0%;
}

.app-dark{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.5);
  color: rgb(12, 12, 12); 
  content: '';
  background: url('./assets/p4.webp') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.search {
  margin-top: 0;
  text-align: center;
  align-items: center;
  padding: 0rem;
}


input {
  margin: 2%;
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255, 0.6);
  background: rgba(255,255,255, 0.1);
  color: #f8f8f8;
}

input::placeholder {
  color: #9a9898;
}

.container {
  max-width: 700px;
  height: 450px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.bg-cs{
  background: linear-gradient(
            rgba(233, 227, 227, 0.5),
            rgba(224, 215, 215, 0.5)
            ), url('./assets/cs.webp') center;
}

.bg-ms{
  background: linear-gradient(
            rgba(255, 255, 255, 0.5),
            rgba(255, 255, 255, 0.5)
            ), url('./assets/ms.webp') center;
}

.bg-dr{
  background: linear-gradient(
            rgba(255, 255, 255, 0.5),
            rgba(255, 255, 255, 0.5)
            ), url('./assets/dr.webp') center;
}

.bg-sm{
  background: linear-gradient(
            rgba(255, 255, 255, 0.5),
            rgba(255, 255, 255, 0.5)
            ), url('./assets/sm.webp') center;
}

.bg-fc{
  background: linear-gradient(
            rgba(255, 255, 255, 0.5),
            rgba(255, 255, 255, 0.5)
            ), url('./assets/fc.webp') center;
}

.bg-rr{
  background: linear-gradient(
            rgba(255, 255, 255, 0.5),
            rgba(255, 255, 255, 0.5)
            ), url('./assets/rr.webp') center;
}

.bg-ts{
  background: linear-gradient(
            rgba(255, 255, 255, 0.5),
            rgba(255, 255, 255, 0.5)
            ), url('./assets/ts.webp') center;
}

.bg-sn{
  background: linear-gradient(
            rgba(255, 255, 255, 0.5),
            rgba(255, 255, 255, 0.5)
            ), url('./assets/sn.webp') center;
}

.bg-bc{
  background: linear-gradient(
            rgba(255, 255, 255, 0.5),
            rgba(255, 255, 255, 0.5)
            ), url('./assets/bc.webp') center;
}

.top {
  width: 100%;
  margin: 0.8rem auto;
}

.description {
  position: relative;
}

.bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 0.5rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255, 0.2);
}

.bold {
  font-weight: 700;
}

.head-btns{
  margin-top: 0;
  margin-bottom: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  justify-content: center;
}

.dark {
  background: black;
  color: white;
}

.pos{
  margin:2%;
  margin-right: 10%;
  display: inline;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.temp-btn{
  margin: 2%;
}

